.btn-v2 {
  margin: 0;
  padding: 0;
  appearance: none;
  display: block;
  position: relative;
  z-index: 1;
  cursor: pointer; }
  .btn-v2::before {
    z-index: 2;
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: -5px;
    height: 50%;
    width: 100%;
    background-color: #6db44a;
    border-radius: 20px;
    transition-duration: .2s;
    box-shadow: -1px 8px 24px -4px rgba(0, 0, 0, 0.21); }
  .btn-v2 .btn-v2__inner {
    position: relative;
    display: flex;
