    top: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    border-radius: 10px;
    background-color: #ff0000;
    background-image: linear-gradient(54deg, rgba(84, 175, 62, 0.97) 0%, rgba(144, 236, 47, 0.97) 100%);
    background-size: 200% 600%;
    background-repeat: no-repeat;
    background-position: 0% 100%;
    transition-duration: .5s;
    animation: btn-v2-bg 5s linear infinite; }
  .btn-v2__text {
    display: block;
    position: relative;
    z-index: 4;
    text-shadow: 0 2px 0 rgba(110, 185, 71, 0.35);
    color: #ffffff;
    font-size: 14px;
    font-weight: 500;
    line-height: 25.23px;
    text-transform: uppercase;
    letter-spacing: 1.07px;
    border-radius: 10px;
    transition-duration: .2s; }
  .btn-v2:hover .btn-v2__inner {
    top: 5px; }
  .btn-v2:hover::before {
    border-radius: 10px; }
  .btn-v2:hover .btn-v2__bg {
    background-size: 100% 100% !important;
    background-position: center center; }

